<template lang="">
<div style="height:100%;">


      <div class="financial-box market-table-header">
        <div>
          <div class="nhsyl"></div>
          <div class="nhsyl-text"></div>
        </div>
        <div>
          <div>最新</div>
        </div>
        <div>
          <div>涨跌</div>
        </div>
      </div>

      <ul class="list-infinite market-table" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" infinite-scroll-immediate-check=false>
        <li v-for="(item,index) in dataList" @click="goDetail(index)">

          <div class="financial-box">
            <div>
              <div class="nhsyl">{{ item.hqzqjc }}</div>
              <div class="nhsyl-text">{{ item.hqzqdm}}</div>
            </div>
            <div>
              <div>{{ item.hqzjcl }}</div>
            </div>
            <div>
              <div>{{zdfunc(index)}}</div>
            </div>
          </div>
        </li>
      </ul>
      <div class="jqqd" v-if="dataList.length==0">
        {{tipMessage}}
      </div>




  <transition name="slide-right">
    <router-view class="cover-transition"></router-view>
  </transition>
</div>
</template>
<script>
import {
  Navbar,
  TabItem,
  InfiniteScroll
} from 'mint-ui';
import {
  Toast
} from "mint-ui";
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
Vue.use(InfiniteScroll);
export default {
  data() {
    return {
      tipMessage:"加载中...",
      loading: false,
      dataList: [],
      indexNow: '',
      pars: {
        epsBgn: "",
        epsEnd: "",
        hqzqdm: "",
        industry: "",
        offset: 0,
        pageSize: global.pageSize,
        recNo: 1,
        regCapitalBgn: "",
        regCapitalEnd: "",
        region: "",
        sortFields: "",
        xyscdm: global.scdm,
        xyywlb: global.STKBIZTYPE.PRICE_BUY_APPL,//定价买入业务类别
      }
    }
  },
  created() {
    this.marketQuote();
  },
  computed: {},
  methods: {
    marketQuote:function(){
      let self = this;
       axiosUtil.quote(this.pars).then(function(data) {
      var data = data.data;
      self.dataList  = data.rows;
      if(self.dataList.length==0){
        self.tipMessage="暂无数据"
      }
    })
    },
    goDetail: function(index) {
         this.$router.push("/market/detail?hqzqdm=" + this.dataList[index].hqzqdm);//跳到报价详情
    },
    zdfunc: function(params) {//涨停
      var data=this.dataList;
      if(data.length!==0){
        var row = data[params];
      return parseFloat(row.hqjrkp) == 0 ? "∞" : (parseFloat(parseFloat(row.hqzjcj).toFixed(4)).subtr(parseFloat(row.hqjrkp))).div(parseFloat(row.hqjrkp));
      }

    },
    loadMore() {
      let self = this;
      this.loading = true;
      this.pars.recNo = this.pars.recNo + 1;
      axiosUtil.quote(this.pars).then(function(data) {
        var data = data.data;

        self.dataList = self.dataList.concat(data.rows);
        self.loading = false;
        if (data.total == self.dataList.length) {
          self.loading = true;
          Toast({
            message: "已加载最后一条",
            position: "bottom",
            duration: 1000
          });
        }

      })
    }
  }
}
</script>
<style media="screen" scoped >
.financial-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
  width: 33.33%;
}

.market-table-header {
  width: 90%;
  margin: 0 auto;
  margin-top: 6px;
  font-size: 12px;
  color: #999;
}

.market-table li {
  padding: 4px;
  border-bottom: 1px solid #eee;
}

.nhsyl-text {
  font-size: 14px;
  color: #999;
}

.mint-tab-container {
  height: 100%;
  padding-bottom: 41px;
}

.my-tab-container2 {
  height: 100%;
}
</style>
<style media="screen">
.mint-tab-container-wrap {
  height: 100%;
}

 ::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

 ::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
</style>



